<template>
 <div class="PostList" v-cloak>

  <div class="loading" v-if="isLoading">
    <img src="../assets/loading.gif" >
  </div>

  <div class="posts" v-else>
    <ul>
      <li>
        <div class="toobar">
          <span>全部</span>
          <span>精华</span>
          <span>分享</span>
          <span>问答</span>
          <span>招聘</span>
        </div>
      </li>
    </ul>

    <ul class="list">
      <li v-for="post in posts">
          <!--头像-->
        <img :src="post.author.avatar_url" alt="">
          <!--回复/浏览-->
        <span class="allcount">
          <span class="reply_count">{{post.reply_count}}</span>
          /{{post.visit_count}}
        </span>
          <!--帖子的分类-->
        <span :class="[{put_good:(post.good  == true),put_top:(post.top  == true),
        'topiclist-tab':(post.good  != true && post.top  != true)}]">
          <span>
            {{post | tabFormatter}}
          </span>
        </span>
          <!--标题-->
        <router-link :to="{
        name:'post_content',
        params:{
          id:post.id,
          name:post.author.loginname
        }
        }">
          <span>
          {{post.title}}
         </span>
        </router-link>
         <!--最終回复时间-->
        <span class="last_reply">
          {{post.last_reply_at | formatDate}}
        </span>
      </li>
    </ul>
   </div>
   <Pagination @changePage="reloadPage($event)"></Pagination>
 </div>
</template>

<script>
import Pagination from './Pagination'

export default {
  data(){
    return{
      isLoading:true,
      posts:[],
      page:1
    }
  },
  beforeMount(){
    this.isLoading = true;
    this.getData();
  },
  methods:{
    getData(){
      this.$http.get('https://cnodejs.org/api/v1/topics',{
        params:{
          page:this.page,
          limit:20,
        }
      }).then((response)=>{
        this.isLoading=false
        this.posts=response.data.data
      }).catch((error)=>{
        console.log(ereror)
      })
    },
    reloadPage(page){
      this.page=page
      this.getData()
    }
  },
  components:{
    Pagination,
  }
}

</script>

<style scoped>

.PostList{
    background-color: #e1e1e1;
    position: relative;
    width:100%;
    margin-top:15px;
  }




.PostList img {
  height: 30px;
  width: 30px;
  vertical-align: middle;
}

.post{
  position: relative;
}
ul {
  position: relative;
  list-style: none;
  width: 100%;
  max-width: 1344px;
  margin: 0 auto;
}

ul li {
  padding: 9px;
  font-size: 15px;
  font-family: "Helvetica Neue", "Luxi Sans", "DejaVu Sans", Tahoma, "Hiragino Sans GB", STHeiti, sans-serif !important;
  font-weight: 400;
  background-color: white;
  color: #333;
  border-top: 1px solid #f0f0f0;
}

.list li:hover {
  background: #f5f5f5;;
}


li span {
  line-height: 30px;
  position: relative;
  left: 2px;
}

.allcount {
  width: 70px;
  display: inline-block;
  text-align: center;
  font-size: 12px;
}

.reply_count {
  color: #9e78c0;
  font-size: 14px;
}

.put_good, .put_top {
  background: #80bd01;
  padding: 2px 4px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -o-border-radius: 3px;
  color: #fff;
  font-size: 12px;
  margin-right: 10px;
}

.topiclist-tab {
  background-color: #e5e5e5;
  color: #999;
  padding: 2px 4px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -o-border-radius: 3px;
  font-size: 12px;
  margin-right: 10px;
}

.last_reply {
  text-align: right;
  min-width: 50px;
  display: inline-block;
  white-space: nowrap;
  float: right;
  color: #778087;
  font-size: 12px;
}

.toobar {
  height: 40px;
  background-color: #f5f5f5;
}
.toobar:nth-child(1){
   
}  
.toobar span {
  font-size: 14px;
  color: #80bd01;
  line-height: 40px;
  margin: 0 10px;
  cursor: pointer;
}

.toobar span:hover {
  color: rgb(7, 65, 104);
}

a {
  text-decoration: none;
  color: black;
}

a:hover {
  text-decoration: underline;
  text-decoration-line: 3px;
}

.loading {
  position: relative;
  text-align: center;
  padding-top: 300px;
}
</style>


